@import '../../assets/scss/imports';

.quickLinks {
    background-color: #ffffff;
    border-radius: 5px;
    margin: 0px 30px 10px;
    overflow: hidden;

    &_title {

        h3 {
            color: $primary;
            margin: 0;
        }
    }

    &_links {
        padding: 10px 0px 20px;
        &_link {
            background-color: $primary;
            height: 35px;
            padding: 0px 25px;
            border-radius: 22px;
            display: inline-flex;
            align-items: center;
            margin-right: 10px;
            cursor: pointer;

            p {
                color: #ffffff;
            }
        }
    }
}